export default {
    data() {
        return {
            providerList: [],
            hasProvider: false,
            username: '',
            password: ''
        };
    },
    methods: {
        initProvider() {
            // const filters = ['weixin', 'qq', 'sinaweibo'];
            const filters=['weixin']
            uni.getProvider({
                service: 'oauth',
                success: res => {
                    if (res.provider && res.provider.length) {
                        for (let i = 0; i < res.provider.length; i++) {
                            if (~filters.indexOf(res.provider[i])) {
                                this.providerList.push({
                                    value: res.provider[i],
                                    image: '../../static/img/' + res.provider[i] + '.png'
                                });
                            }
                        }
                        this.hasProvider = true;
                    }
                },
                fail: err => {
                    // console.error('获取服务供应商失败：' + JSON.stringify(err));
                }
            });
        },
        initPosition() {
            /**
             * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
             * 反向使用 top 进行定位，可以避免此问题。
             */
            this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
        },
        bindLogin() {
            if (this.username.length < 1) {
                uni.showToast({
                    icon: 'none',
                    title: '用户名不允许为空'
                });
                return;
            }
            if (this.password.length < 0) {
                uni.showToast({
                    icon: 'none',
                    title: '密码不允许为空'
                });
                return;
            }
            uni.request({
                url: 'http://zhangzibao.xyz:8080/myqqServer/api/login',
                data: {
                    username: this.username,
                    password: this.password
                },
                header: {
                    'content-type': 'application/json'
                },
                method: 'POST'
            })
                .then(([err, res]) => {
                    uni.showLoading({
                        title: '登录中'
                    });
                    if (res.statusCode === 200) {
                        this.toMain(res.data);
                    } else {
                        uni.hideLoading()
                        uni.showToast({
                            icon: 'none',
                            title: res.data
                        });
                    }
                })
                .catch(e => {
                    uni.showToast({
                        icon: 'none',
                        title: '网络连接错误'
                    });
                });
        },
        oauth(value) {
            uni.showLoading({
                title: '登录中'
            });
            uni.login({
                provider: value,
                success: res => {
                    uni.getUserInfo({
                        provider: value,
                        success: infoRes => {
                            /**
                             * 实际开发中，获取用户信息后，需要将信息上报至服务端。
                             * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
                             */
                            uni.request({
                                url: 'http://zhangzibao.xyz:8080/myqqServer/api/oauth',
                                data:infoRes.userInfo,
                                header: {
                                    'content-type': 'application/json'
                                },
                                method: 'POST'
                            })
                                .then(([err, res]) => {

                                    if (res.statusCode === 200) {
                                        this.toMain(res.data);
                                    } else {
                                        uni.showToast({
                                            icon: 'none',
                                            title: res.data
                                        });
                                    }
                                })
                                .catch(e => {
                                    uni.hideLoading();
                                    uni.showToast({
                                        icon: 'none',
                                        title: '网络连接错误'
                                    });
                                });
                        }
                    });
                },
                fail: err => {
                    uni.hideLoading();
                    uni.showToast({
                        icon: 'none',
                        title: '授权登录失败，请使用其他登录方式'
                    });
                }
            });
        },
        toMain(userInfo) {
            this.$store.dispatch('Login', userInfo).then(res => {
                uni.hideLoading();
                uni.reLaunch({
                    url: '../user/user'
                });
                // console.log('登录成功');
            });
        }
    },
    onLoad() {
//         this.initPosition();
//         this.initProvider();
    }
};
